.footer {
  position: fixed;
  display: flex;
  justify-content: space-between;
  bottom: 0;
  height: 55px;
  width: 98%;
  background-color: #282828;
  padding: 20px;
}

.footer_left {
  flex: 0.3;
  color: white;
  display: flex;
  align-items: center;
  max-width: 300px;
}

.footer_center {
  flex: 0.4;
  padding: 0 100px;
  color: white;
  display: flex;
  align-items: center;
  justify-content: space-between;
  max-width: 300px;
}

.footer_right {
  flex: 0.3;
  display: flex;
  color: white;
  align-items: center;
  justify-content: space-between;
}

.footer_albumnlogo {
  height: 60px;
  width: 60px;
  margin-right: 20px;
  object-fit: contain;
}

.footer_green {
  color: green;
}

.footer_songinfo > h4 {
  margin-bottom: 5px;
}

.footer_songinfo > p {
  font-size: 12px;
}

.footer_icon:hover,
.footer_green:hover {
  transition: transform 200ms ease-in-out;
  transform: scale(1.2) !important;
}

.slider_green {
  color: green;
}
